function sliderMain() {
    new Swiper(".main__slider .mySwiper",{
        slidesPerView: 1,
        spaceBetween: 30,
        loop: true,
        navigation: {
            nextEl: ".main__slider .swiper-button-next",
            prevEl: ".main__slider .swiper-button-prev"
        },
        pagination: {
            el: ".main__slider .swiper-pagination",
            clickable: true,
        },
    })
}

function sliderMain2() {
    new Swiper(".main__slider2 .mySwiper",{
        slidesPerView: 6,
        spaceBetween: 32,
        loop: true,
        breakpoints: {
            320: {
                slidesPerView: 2,
                spaceBetween: 10,
            },
            577: {
                slidesPerView: 2,
                spaceBetween: 18,
            },
            993: {
                slidesPerView: 5,
                spaceBetween: 24,
            },
            1401: {
                slidesPerView: 6,
                spaceBetween: 32,
            }
        }
    })
}

function sliderInfo() {
    new Swiper(".info .mySwiper",{
        slidesPerView: "auto",
        spaceBetween: 32,
        pagination: {
            el: ".info .swiper-pagination",
            type: "progressbar",
        },
    })
}

function sliderCarsHead() {
    var swiper = new Swiper('.cars__slider .mySwiper', {
        slidesPerView: 'auto',
        spaceBetween: 8,
        slideToClickedSlide: 'true',
        breakpoints: {
            320: {
                spaceBetween: 4,
            },
            993: {
                spaceBetween: 8,
            }
        }
    })
}

function sliderCarsBody() {
    var swiper = new Swiper('.cars__slider2 .mySwiper', {
        slidesPerView: 4,
        spaceBetween: 8,
        slideToClickedSlide: 'true',
        pagination: {
            el: ".cars__slider2 .swiper-pagination",
            type: "progressbar",
        },
        breakpoints: {
            320: {
                slidesPerView: 'auto',
                spaceBetween: 15,
            },
            769: {
                slidesPerView: 3,
                spaceBetween: 15,
            },
            993: {
                slidesPerView: 4,
                spaceBetween: 30,
            }
        }
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        slidesPerView: 4,
        spaceBetween: 8,
        freeMode: true,
        watchSlidesProgress: true,
        direction: "vertical",
        breakpoints: {
            320: {
                direction: "horizontal",
            },
            769: {
                direction: "vertical",
            }
        }
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 16,
        slidesPerView: 1,
        thumbs: {
            swiper: swiper,
        },
    });
}

$(document).ready(function () {
    sliderMain();
    sliderMain2();
    sliderInfo();
    sliderCarsHead();
    sliderCarsBody();
    gallery();


    // Fixed-header
    $(function() {
        if (window.matchMedia("(min-width: 993px)").matches) {
            let header = $('.header');
            let hederHeight = header.height(); // вычисляем высоту шапки

            $(window).scroll(function() {
                if($(this).scrollTop() > 91) {
                    header.addClass('header-fixed');
                    $('body').css({
                        'paddingTop': hederHeight+'px' // делаем отступ у body, равный высоте шапки
                    });
                } else {
                    header.removeClass('header-fixed');
                    $('body').css({
                        'paddingTop': 0 // удаляю отступ у body, равный высоте шапки
                    })
                }
            });
        }
    });

    //Menu
    $('.burger').click(function () {
        $('.header__center').addClass('header__center--active')
        $('body').addClass('fixed-body')
    })

    $('.header__close').click(function () {
        $('.header__center').removeClass('header__center--active')
        $('body').removeClass('fixed-body')
    })

    $(".header__menu-item").click(function () {
        $(this).siblings(".header__menu-item").removeClass("header__menu-item--active")
        $(this).addClass("header__menu-item--active")
    })

    $('.header__menu li').click(function () {
        $('.header__menu').removeClass('header__menu--active')
        $('.header__center').removeClass('header__center--active')
        $('body').removeClass('fixed-body')
    })

    $('.header__check').click(function () {
        $('body').toggleClass('dark-bg')
    })

    //Show-more
    $('.show-more').click(function () {
        $('.feedback').find('.hidden-content').addClass('hidden-content--active')
    })

    // Numbers
    $(document).ready(function () {
        var a = 0;
        $(window).scroll(function () {
            var oTop = $(".about").offset().top - window.innerHeight;
            if (a == 0 && $(window).scrollTop() > oTop) {
                $(".counter-value").each(function () {
                    var $this = $(this),
                        countTo = $this.attr("data-count");
                    $({
                        countNum: $this.text(),
                    }).animate(
                        {
                            countNum: countTo,
                        },
                        {
                            duration: 2000,
                            easing: "swing",
                            step: function () {
                                $this.text(Math.floor(this.countNum));
                            },
                            complete: function () {
                                $this.text(this.countNum);
                            },
                        }
                    );
                });
                a = 1;
            }
        });
    });

    //Mask
    $(".phone-mask").mask("+7(999) 999-99-99");

    //Quiz
    $('.quiz-next').click(function (e) {

        let activeStep =$('.quiz-step--active').attr('data-step')

        var $current = $('.quiz-step--active');

        $current.removeClass('quiz-step--active');
        $current.next('.quiz-step').addClass('quiz-step--active');
        if ( $('.quiz-step').last().hasClass('quiz-step--active') ) {
            $('.quiz__group').addClass('remove')
        }

        if (activeStep === '1') {
            $('.quiz-step').parents('.quiz').find('.progress-bar').css('width', '33.3%')
            $('.quiz-step').parents('.quiz').find('.progress-percentage').html(33.3)
        }

        if (activeStep === '2') {
            $('.quiz-step').parents('.quiz').find('.progress-bar').css('width', '66.6%')
            $('.quiz-step').parents('.quiz').find('.progress-percentage').html(66.6)
        }

    })

    // Accordion
    $(".item-show").click((function(){
        $(this).toggleClass("item-show--active")
        $(this).siblings(".item-hidden").slideToggle()
    }));

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})